.scrolly-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  padding: 20px;
}

.text-section {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative; /* Necessary to stack text on top of each other */
}

.text-item {
  opacity: 0;
  position: absolute;
  transition: opacity 0.7s ease-in-out;
}

.text-item.active {
  opacity: 1;
}

.image-section {
  position: sticky;
  top: 50%;
  transform: translateY(-50%); /* Centers the image vertically */
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; /* For overlapping images */
}

.scrolly-image {
  max-width: 100%;
  height: auto;
  opacity: 0;
  position: absolute;
  transition: opacity 0.7s ease-in-out;
}

.scrolly-image.active {
  opacity: 1;
}




