.tooltip {
  position: relative;
  display: inline-block;
  /*border-bottom: 1px dotted #ccc;
    color: #006080; */
}

.tooltip .tooltiptext.light {
  background-color: #ffffff;
  color: #8a4646;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.tooltip .tooltiptext.dark {
  background-color: #ffffff0a;
  color: #ffffff;
}

.tooltip .tooltiptext {
  font-family: "RoobertLight", sans-serif;
  visibility: hidden;
  position: absolute;
  width: 320px;
  text-align: left;
  padding: 10px 15px;
  border-radius: 12px;
  z-index: 1;
  line-height: 1.3em;
  opacity: 0;
  transition: opacity 0.3s;
  border: 2px solid rgba(255, 255, 255, 0.085);
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

.tooltip-right {
  top: -5px;
  left: 125%;
}

.tooltip-right::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent #555 transparent transparent;
}

.tooltip-bottom {
  top: 135%;
  margin-left: -20px;
}

.tooltip-top {
  bottom: 125%;
  left: 50%;
  margin-left: -60px;
}

.tooltip-top::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.tooltip-left {
  top: -5px;
  bottom: auto;
  right: 128%;
}
.tooltip-left::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent #555;
}

.arrow-t {
  position: absolute;
  left: 48%;
  top: -10px;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 10px solid #fff;
  z-index: 1;
}

