.dump-sec p {
  font-family: "Roobert-Regular",sans-serif;
  color: rgb(255, 255, 255);
  width: calc(20vw + 20vh + 20vmin);
  font-size: 20px;
  margin-block-start: 0em;
  margin-block-end: 0em;
  font-size: calc(0.5vw + 0.8vh + 0.5vmin);
 
  line-height: calc(1vw + 1vh + 1vmin);
}
#section{
  background-image: linear-gradient(360deg, #0057ff24, #0057ff00);
}

.dump-sec {
  height: 100vh;
  width: 80%;
  display: flex;
  flex-direction: row;
  margin-left: auto;
  margin-right: auto;
  justify-content: space-between;
  align-items: center;
  
}

.dump-sec h1 {
  font-family: "EditorialLight", sans-serif;
  color: white;
width: 70vh;
  font-size: calc(2vw + 2vh + 1vmin);
  margin-block-start: 0em;
  margin-block-end: 0em;
  
  font-weight: 100;
  font-style: normal;
}
.dumpyard-text {
  font-family: "EditorialLight", sans-serif;
  color: white;
  text-transform: uppercase;
  font-size: calc(5vw + 5vh + 5vmin);
  margin-block-start: 0em;
  margin-block-end: 0em;
  display: block;
  font-weight: 100;
  font-style: normal;
}

.backtohome-dump {
  float: right;
  text-decoration: none;
  font-family: "Roobert-Regular",sans-serif;
  font-size: calc(0.4vw + 0.6vh + 0.6vmin);
  color: white;
  text-align: center;
  padding: 10px 30px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.432);
  transition: all 0.2s ease 0s;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 50px;
  text-decoration: none;
  color: whitesmoke;
  text-transform: uppercase;
}

.backtohome-dump:hover,
.backtohome-dump:focus {
  border: 1px solid rgba(255, 255, 255, 0);
  box-shadow: 0 1px 30px rgba(113, 64, 242, 0.8);
  cursor: url("../Assets/cursor.svg"), auto;
  transform: translateY(-0.1em);
}
.navbar-container {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
}
.dumpyard p {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  font-family: "Roobert-Regular";
  font-size: calc(0.8vw + 0.6vh + 0.6vmin);
  color: white;
  margin-block-start: 0em;
  margin-block-end: 1em;
  width: 50%;
}
.dump-image {
  width: 100%;
  height: auto;
  background-color: blueviolet;
  border-radius: 10px;
}
.dump-image-2 {
  width: 100%;
  height: 85vh;
  background-color: blueviolet;
}

.assembly {
  font-family: "DrukPort", sans-serif;
  font-size: calc(5vw + 5vh + 5vmin);
  text-transform: uppercase;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  display: block;
  position: absolute;
  bottom: 0;
  transform: translate(-50%, 0%);
  left: 50%;
  color: white;
  font-weight: 400;
  font-style: normal;
}
.assembly-2 {
  font-family: "DrukPort", sans-serif;
  font-size: calc(5vw + 5vh + 5vmin);
  text-transform: uppercase;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  display: block;
  position: absolute;
  bottom: 0;
  transform: translate(-50%, 0%);
  left: 50%;
  color: #fff500;
  width: 100%;
  font-weight: 400;
  font-style: normal;
}
.assembly-3 {
  font-family: "DrukPort", sans-serif;
  font-size: calc(5vw + 5vh + 5vmin);
  text-transform: uppercase;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  display: block;
  position: absolute;
  bottom: 0;
  transform: translate(-50%, 0%);
  left: 50%;
  color: #00ffb2;
  font-weight: 400;
  font-style: normal;
}

.dump-button {
  margin-top: 20px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}
.images {
  width: 40%;
  height: auto;
}
.button2 {
  text-decoration: none;
  font-family: "Roobert-Regular";
  font-size: calc(0.4vw + 0.4vh + 0.5vmin);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.432);
  padding: 8px 20px;
  border-radius: 12px;
  transition: all 0.2s ease 0s;
  margin-left: 10px;
  align-items: center;
  text-align: center;
  display: flex;
  gap: 10px;
}
.download {
  width: 25px;
}
.button2:hover,
.button2:focus {
  border: 1px solid rgba(255, 255, 255, 0);
  box-shadow: 0 1px 30px rgba(113, 64, 242, 0.8);
  cursor: url("../Assets/cursor.svg"), auto;
  transform: translateY(-0.1em);
}
.responsive-grid {
  display: flex;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  grid-gap: 20px;
}

.dumpyard-container {
  width: 95vw;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-device-width: 800px) {
  .dumpyard {
    margin-top: 30px;
  }
  .backtohome-dump {
    float: right;
    text-decoration: none;
    font-family: "Roobert-Regular";
    font-size: calc(1.5vw + 1.2vh + 1vmin);
    color: white;
    text-align: center;
    padding: 10px 25px;
    transition: all 0.2s ease 0s;
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 50px;
  }
  .dump-sec p {
    font-family: "Roobert-Regular";
    color: rgb(255, 255, 255);
    width: 100%;
    line-height: calc(1vw + 2vh + 2vmin);
    font-size: calc(1.5vw + 1vh + 1.55vmin);
    margin-block-start: 0em;
    margin-block-end: 0em;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
  }
  .dumpyard p {
    line-height: calc(1vw + 2vh + 2vmin);
    font-size: calc(1.5vw + 1vh + 1.55vmin);
    width: 90%;
  }
  .responsive-grid {
    display: flex;
    gap: 10px;

    flex-direction: column;
  }
  .dump-sec {
    margin-top: 20vh;
    height: 100vh;
    width: 93vw;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    gap: 50px;
    justify-content:left
  }

  .dump-sec h1 {
    width: 90%;
    
    font-size: calc(2vw + 3.5vh + 1vmin);
    margin-block-start: 0.5em;
  }
  .navbar-container {
    width: 100%;
    display: flex;
    align-items: center;
  }
  .dump-image {
    width: 100%;
    height: auto;
    margin-top: 0px;
    border-radius: 5px;
  }
  .dump-image-2 {
    width: 100%;
    height: 40vh;
    margin-top: 0px;
  }
  .images {
    width: 100%;
    height: auto;
  }
  .button2 {
    font-size: calc(1vw + 1vh + 1vmin);

    border: 1px rgba(255, 255, 255, 0.196) solid;

    border-radius: 12px;
    padding: 10px 20px;
    margin-left: 0px;
    margin-right: 10px;
  }
  .dump-button {
    position: relative;
    left: 0;
    margin-left: 0;
    margin-right: 0;
  }
}
