.contact-div {
  margin-top: 12%;

  text-align: center;
  
}
.get-in-touch {
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.talk{
  font-family: "Roobert-Regular";
  font-size: calc(1vw + 0.3vh + 0.2vmin);
  color: whitesmoke;
  margin-block-end: 1em;
}

.get-in-touch h2 {
 
  font-size: calc(5vw + 5vh + 5vmin);
  font-family: "EditorialLight", sans-serif;
text-decoration: none;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  margin-block-start: 0.1em;
  margin-block-end: 0em;
  font-weight: 400;
  font-style: normal;
}
.get-in-touch a:hover {
  cursor: url("../Assets/open.svg"), auto;
}
.get-in-touch h2 a{
  color: white;
  text-decoration: none;
}
.mask {
  width: 50vw;

  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  border-radius: 100px;
  border: 1px solid white;
  padding: 10px 10px 10px;
  box-shadow: 0 1px 30px rgba(114, 64, 242, 0);
}

.marquee-contact {
  white-space: nowrap;
  overflow: hidden;
  display: inline-block;
  animation: marquee 20s linear infinite;
}

.marquee-contact p {
  display: block;
  margin-block-start: 0.1em;
  margin-block-end: 0.0001em;
}
.marquee-contact p h1 {
  font-family: "HostLight";
  font-size: calc(2vw + 3vh + 2vmin);
  letter-spacing: 0.8px;

  color: #0057FF;
}

.marquee-contact p {
  display: inline-block;
}

.footer-left{
  display: flex;
  font-family: "Roobert-Regular";
  font-size: calc(0.5vw + 0.5vh + 0.5vmin);
flex-wrap: wrap;
 justify-content: center;
 gap: 10px;


}

.footer-left p{

  color: rgba(244, 238, 253, 0.484);

}
@keyframes marquee {
  0% {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(-50%, 0, 0);
  }
}

.contact-div ul {
  font-family: "Roobert-Regular";
  text-transform: uppercase;
  color: white;
  list-style: none;
  font-size: calc(1vw + 0.5vh + 0.5vmin);
  display: flex;
  justify-content: center;
  gap: 50px;
  margin-top: 50px;
  margin-bottom: 50px;
}
.contact-div ul li a{
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 5px 20px;
  border-radius: 50px;
  text-decoration: none;
  color: whitesmoke;

}

.contact-div ul li a:hover,
.contact-div ul li a:focus {
  border: 1px solid rgba(255, 255, 255, 0);
  box-shadow: 0 1px 30px rgba(113, 64, 242, 0.8);
  cursor: url("../Assets/cursor.svg"), auto;
  transition: all 0.2s ease 0s;
  transform: translateY(-0.10em);
}
.copy-to-clipboard {
  width: 25%;
  height: auto;
  position: relative;
  float: right;

  margin-bottom: 20px;
}
.email {
  background-color: #7140f2;
}
.email-img {
  background-color: #121222;
}
.success-copy {
  position: relative;
  color: white;
  background-color: #7140f2;
  padding: 8px 24px;
  font-size: calc(0.6vw + 0.4vh + 0.5vmin);
  font-family: "Roobert-Regular";
  z-index: 9999;
  border-radius: 50px;
  animation: slideUp 0.4s cubic-bezier(0.4, 0, 0.2, 1) both;
  margin-left: auto;
  margin-right: auto;
  bottom: -25vh;
  width: 100px;
}
@keyframes slideUp {
  0% {
    transform: translateY(50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}


@media (max-width: 1200px) {
  .contact-sub {
    width: 90%;
  }

  .contact-div h2 {
    width: 80%;
  }

  .footer-main {
    width: 100%;
    left: 30px;
  }
  .container-1 {
    width: 36vw;
    height: 120px;
  }

  .text {
    left: 120px;
  }
  .text h3 {
    font-size: 18px;
  }
  .text p {
    font-family: "Roobert-Regular";
    font-size: 16px;
    width: 100%;
  }
  .icon-div {
    border-radius: 12px;
    height: 100px;
    width: 90px;
  }
  .copy-to-clipboard {
    width: 30%;
    right: 10%;
  }
}



@media (max-width: 800px) {
  .talk{
    font-family: "Roobert-Regular";
    font-size: calc(1vw + 1.5vh + 0.2vmin);
    color: whitesmoke;
    margin-block-end: 0.5em;
  }

  
  .get-in-touch h2 {
    font-size: calc(8vw + 6vh + 6vmin);
    margin-left: auto;
    margin-right: auto;
  }
  .get-in-touch a {
    font-size: calc(10vw + 6vh + 6vmin);
    color: rgb(255, 255, 255);
    text-decoration: underline;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    margin-block-start: 0.1em;
    margin-block-end: 0.1em;

    font-family: "DrukPort", sans-serif;
    font-weight: 400;
    font-style: normal;
  }


  .contact-sub {
    position: relative;
    font-family: "Roobert-Regular";
    left: 10px;
    color: rgb(187, 184, 214);
    margin-block-start: 0.1em;
    margin-block-end: 1.5em;
    width: 90vw;
    font-size: 16px;
  }

  .copy-to-clipboard {
    width: 70%;
    right: 10%;
  }
  .footer-main {
    display: flex;
    flex-direction: column;
    gap: 0px;
  }
  .container-1 {
    min-width: 100%;
    width: 100%;
    height: 110px;
    margin-bottom: 10px;
  }
  .text {
    left: 25px;
  }


  .contact-div h2 {
    font-family: "BasementGrotesk";
    font-size: 40px;
    color: white;
    text-align: center;
    width: 95%;
    
    margin-block-start: 0.8em;
    margin-block-end: 0.5em;
  }
  .footer {
    position: relative;
    margin-top: 20px;
    padding-bottom: 20px;
    display: flex;
  
  
  }
  .footer-left {
    text-align: center;
    justify-content: center;
   width: 80%;
    font-size: calc(1vw + 1vh + 1vmin);
  gap: 20px;
  }
  .footer-left p{
    line-height: calc(1vw + 1vh + 3vmin);
    margin-block-start: 0em;
    margin-block-end: 0em;
  }
  .success-copy {
    position: relative;
    background-color: #7140f2;
    padding: 8px 24px;
    font-size: 18px;
    z-index: 9999;
    border-radius: 50px;
    bottom: -40vh;
    left: 0vh;
  }
  .marquee-contact {
    animation: marquee 40s linear infinite;
  }
  
 
  .marquee-contact p h1 {
    font-size: calc(1.5vw + 3vh + 2vmin);
 
  }
  
  .mask {
    width: 80vw;
  }
  .contact-div ul {
    font-family: "Roobert-Regular";
    text-transform: uppercase;
    color: white;
    list-style: none;
    font-size: calc(1vw + 2vh + 1vmin);
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    margin-top: 50px;
    margin-bottom: 50px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    width: 50%;
    padding-inline-start: 0px;

  }
  .contact-div ul li a{
    border: 1px solid rgba(255, 255, 255, 0.1);
    padding: 5px 20px;
    border-radius: 50px;
    text-decoration: none;
    color: whitesmoke;
  
  }
  .footer-left{
    display: flex;
    font-family: "Roobert-Regular";

  flex-wrap: wrap;
   justify-content: center;
   gap: 10px;
  margin-left: auto;
  margin-right: auto;
  }
}
